<template>
    <div id="Main">
		<el-container>
		  <el-header>
			  <div style="background-color: #545c64; width: 100%;">
				  <el-menu
				    class="el-menu-demo"
				    mode="horizontal"
				    @select="handleSelect"
					background-color="#545c64"
				    text-color="#fff"
				    active-text-color="#ffd04b">
				  				<el-submenu index="1">
				  					<template slot="title">{{username}}</template>
				  					<el-menu-item index="1-1" @click="toUserMsg">个人信息</el-menu-item>
				  				</el-submenu>
				    <el-submenu index="2">
				      <template slot="title">购买记录</template>
				      <el-menu-item index="2-1">已购买</el-menu-item>
				      <el-menu-item index="2-2">我的足迹</el-menu-item>
				    </el-submenu>
				    <el-submenu index="3">
				  					<template slot="title">购物车</template>
				  					<el-menu-item index="3-1" @click="shopcart">购物车</el-menu-item>
				  				</el-submenu>
				    <el-submenu index="4">
				  	  <template slot="title">收藏</template>
				  	  <el-menu-item index="4-1">收藏的店铺</el-menu-item>
				  	  <el-menu-item index="4-2">收藏的商品</el-menu-item>
				    </el-submenu>
				  				<el-menu-item index="5" @click="search">搜索</el-menu-item>
				  				<el-menu-item index="6" @click="returnTomain">主页</el-menu-item>
				    <el-menu-item index="7" @click="signout">注销</el-menu-item>
				  </el-menu>
			  </div>
		  </el-header>
		  <br />
		  
		  <router-view></router-view>
		  
		</el-container>
	</div>
</template>

<script>
    export default {
		name: 'Main',
		data:function() {
			return {
				username: this.$route.query.username,
				
			}
		},
		methods:{
			signout() {
				this.$router.replace('/');
			},
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			},
			handleSelect(key, keyPath) {
				console.log(key, keyPath);
			},
			search() {
				this.$router.push({
					path: '/search',
					query: {
						username: this.username
					}
				});
			},
			shopcart() {
				this.$router.push('/cart');
			},
			toUserMsg() {
				this.$router.push('/userMsg');
			},
			returnTomain() {
				this.$router.replace({
					path: '/main',
					query: {
						username: this.username
					}
				});
			}
		}
    }
</script>

<style>
	body {
		background-image: '';
		background-size: 100%;
		background-repeat: no-repeat;
		background-position: center center;
	}
	.el-menu-demo {
		float: right;
	}
	.main {
		height: 100%;
	}
</style>