<template>
	<div id="login">
		<el-container>
		  <el-header>
			  <span>校园二手市场</span>
			  <span>请先登录</span>
		  </el-header>
		  <el-container>
		    <el-aside width="200px">
				<img src="../../assets/logo.png" />
			</el-aside>
		    <el-container>
		      <el-main>
				  <el-form ref="loginData"
				  	class="login-user"
				  	:model="loginData"
				  	:rules="rules"
				  	label-width="80px">
				  	<el-form-item label="用户名" prop="username">
				  		<el-input v-model="loginData.username" ></el-input>
				  	</el-form-item>
				  	<el-form-item label="密码" prop="userpass">
				  		<el-input type="password" v-model="loginData.userpass" ></el-input>
				  	</el-form-item>
					<el-form-item label="商家登录" prop="isStore">
						<el-switch v-model="loginData.isStore" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
					</el-form-item>
				  	<el-form-item label="验证码" style="display: inline;">
				  		<el-input @keyup.enter.native="submit('loginData')"
							v-model="input"></el-input>
						<div class="ValidCode disabled-select" :style="`width:${width}; height:${height}`" @click="refreshCode" align="center">
							<span v-for="(item, index) in codeList" :key="index" :style="getStyle(item)">{{item.code}}</span>
						</div>
				  	</el-form-item>
				  	<br />
				  	<el-form-item>
				  		<el-button type="primary" @click="submit('loginData')">登录</el-button>
				  		<el-button @click="cancel">取消</el-button>
				  	</el-form-item>
				  </el-form>
				  <el-button type="primary" @click="register">注册</el-button>
			  </el-main>
		      <el-footer>
				  <span>广告位招租</span>
			  </el-footer>
		    </el-container>
		  </el-container>
		</el-container>
	</div>
</template>

<script>
	export default {
		name: 'login',
		props: {
			width: {
				type: String,
				default: '100px'
			},
			height: {
				type: String,
				default: '40px'
			},
			length: {
				type: Number,
				default: 4
			}
		},
		data:function() {
			return {
				loginData: {
					username: '',
					userpass: '',
					isStore: false,
				},
				rules: {
					username: [{
						required: true,
						message: '请输入用户名',
						trigger: 'blur'
					}],
					userpass: [{
						required: true,
						message: '请输入密码',
						trigger: 'blur'
					}]
				},
				codeList: [],
				input: '',
			}
		},
		methods: {
			submit(loginData) {
				if (this.loginData.isStore) {
					this.$router.replace({
						path: '/storemain',
						query: {
							username: this.loginData.username
						}
					});
				} else {
					this.$router.replace({
						path: '/main',
						query: {
							username: this.loginData.username
						}
					});
				}
			},
			register() {
				this.$router.replace('/register');
			},
			cancel() {
				this.$refs.loginData.resetFields();
			},
			refreshCode() {
				this.createCode();
			},
			createCode() {
				let len = this.length,
				codeList = [],
				chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz0123456789',
				charsLen = chars.length
				// 生成
				for (let i = 0; i < len; i++) {
					let rgb = [Math.round(Math.random() * 220), Math.round(Math.random() * 240), Math.round(Math.random() * 200)]
					codeList.push({
					code: chars.charAt(Math.floor(Math.random() * charsLen)), // 随机码
					color: `rgb(${rgb})`, // 随机颜色
					fontSize: `1${[Math.floor(Math.random() * 10)]}px`, // 随机字号
					padding: `${[Math.floor(Math.random() * 10)]}px`, // 随机内边距
					transform: `rotate(${Math.floor(Math.random() * 90) - Math.floor(Math.random() * 90)}deg)` // 随机旋转角度
					})
				}
				// 指向
				this.codeList = codeList
				// 将当前数据派发出去
				this.$emit('update:value', codeList.map(item => item.code).join(''))
			},
			getStyle (data) {
				return 'color: ${data.color}; font-size: ${data.fontSize}; padding: ${data.padding}; transform: ${data.transform}'
			},
		}
	}
</script>

<style>
	body {
		background-image: url(../../assets/timg2.jpg);
		background-size: 100%;
		background-repeat: no-repeat;
		background-position: center center;
	}
	.ValidCode {
		border-style: solid;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		span{
			display: inline-block;
		}
	}
	.login-user {
		width: 400px;
	}
</style>
