<template>
	<div id="goodlist">
		<el-table
			:data="tableData"
			class="shopCar">
			<el-table-column prop="imgurl"
				label="图片" width="80">
				<template slot-scope="scope">
					<img :src="scope.row.imgurl" width="20px" height="20px"/>
				</template>
			</el-table-column>
			<el-table-column prop="goodname"
				label="商品名称" width="80">
			</el-table-column>
			<el-table-column prop="price"
				label="价格" width="80">
			</el-table-column>
			<el-table-column prop="count"
				label="数量">
				<template slot-scope="scope">
					<el-button @click="del(scope.row)" :disabled='scope.row.count <= 1'>-</el-button>
					<span>{{scope.row.count}}</span>
					<el-button @click="add(scope.row)">+</el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default {
		data:function() {
			return {
				tableData:[{
					imgurl: require('../../assets/logo.png'),
					goodname: '手表',
					price: 20,
					count: 1,
				},{
					imgurl: require('../../assets/logo.png'),
					goodname: '电脑',
					price: 2000,
					count: 1,
				},{
					imgurl: require('../../assets/logo.png'),
					goodname: 'java全栈教程',
					price: 30,
					count: 1,
				}]
			}
		},
		methods: {
			add(index) {
				index.count++;
			},
			del(index) {
				index.count--;
			},
			print() {
				console.log(this);
			}
		}
	}
</script>

<style>
	.shopCar {
		width: 100%;
	}
</style>
