<template>
	<div id="addAndsub">
		<div id="add">
			
			<el-row :gutter="20">
				<el-col :span="12" :offset="6">
					<el-button type="primary" @click="dialogVisible = true">新增商品</el-button>
				</el-col>
			</el-row>
			
				<el-dialog
				  title="提示"
				  :visible.sync="dialogVisible"
				  width="30%"
				  :before-close="handleClose">
				  
				  <el-form ref="form" :model="Form" label-width="80px" size="mini">
					<el-form-item label="商品名称">
						<el-input v-model="Form.goodname"></el-input>
					</el-form-item>
					<el-form-item label="商品登记数量">
						<el-button @click="del()" :disabled='Form.count<= 1'>-</el-button>
						<span>{{Form.count}}</span>
						<el-button @click="add()">+</el-button>
					</el-form-item>
					<el-form-item label="商品价格">
						<el-input v-model="Form.price"></el-input>
					</el-form-item>
					<el-form-item label="图片地址">
						<el-upload
						  class="upload-demo"
						  drag
						  action="https://jsonplaceholder.typicode.com/posts/"
						  multiple>
						  <i class="el-icon-upload"></i>
						  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
						  <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
						</el-upload>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="emitData('Form')">提交商品信息</el-button>
						<el-button @click="dialogVisible = false">取消</el-button>
					</el-form-item>
				  </el-form>
				</el-dialog>
		</div>
		<br />
		<div id="else">
			<el-table
				:data="tableData"
				class="shopCar">
				<el-table-column prop="imgurl"
					label="图片" width="80">
					<template slot-scope="scope">
						<img :src="scope.row.imgurl" width="20px" height="20px"/>
					</template>
				</el-table-column>
				<el-table-column prop="goodname"
					label="商品名称" width="80">
				</el-table-column>
				<el-table-column prop="price"
					label="价格" width="80">
				</el-table-column>
				<el-table-column prop="count"
					label="数量">
				</el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
					        <el-button
					          size="mini"
					          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					        <el-button
					          size="mini"
					          type="danger"
					          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
					      </template>
				</el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
  export default {
    data() {
      return {
        dialogVisible: false,
		Form: {
			goodname: '',
			count: '1',
			imgUrl: '',
			price: '',
		},
		tableData:[{
			imgurl: require('../../assets/logo.png'),
			goodname: '手表',
			price: 20,
			count: 1,
		},{
			imgurl: require('../../assets/logo.png'),
			goodname: '电脑',
			price: 2000,
			count: 1,
		},{
			imgurl: require('../../assets/logo.png'),
			goodname: 'java全栈教程',
			price: 30,
			count: 1,
		}]
      };
    },
    methods: {
		handleClose(done) {
			this.$confirm('确认关闭？').then(_ => {
					done();
				}).catch(_ => {});
		},
		emitData(Form) {
			console.log(this.Form);
			this.$confirm('确认提交？').then(_ => {
				this.dialogVisible = false;
					done();
				}).catch(_ => {});
			
			
		},
		del() {
			this.Form.count--;
		},
		add() {
			this.Form.count++;
		},
		handleDelete(index,row) {
			console.log(index);
			console.log(row)
		},
		handleEdit(index,row) {
			console.log(index);
			console.log(row);
		}
    }
  };
</script>

